.project-item-container {
    width: 339px;
    background: #ffffff;
    box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.06);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    margin: 15px;
  }


  .project-item-container {
    position: relative;
    background-color: #f0f0f0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease; /* Add transition for smooth movement */
  }
  
  .project-item-container {
    transform: translateY(-10px); /* Move the card up on hover */
  }
  



  
  .project-item-image {
    width: 100%;
    border: 1px solid transparent;
    border-radius: 5px;
    height: 30vh;
  }
  
  .project-item-details-container {
    padding: 32px 24px 32px 24px;
  }
  
  .project-item-title {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #183b56;
    margin: 0;
  }
  
  .project-item-description {
    font-family: 'Roboto';
    font-size: 18px;
    line-height: 32px;
    margin-top: 12px;
    margin-bottom: 0;
    color: #7b8794;
  }
  
  .project-link {
    text-decoration: none;
    color: white;
  }
  
  .project-link:hover {
    box-shadow: 0 0 10px rgb(239, 241, 242); 
  }
  