* {
  box-sizing: border-box;
}

body {

  padding-bottom:5%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', ' Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-image: url('https://res.cloudinary.com/dhzqndogf/image/upload/v1707469415/dark-geometric-background-with-copy-space_nijseg.jpg');
  background-size: cover;
  overflow-x: hidden;
}

html {
  overflow-x: hidden;
}


.card-description {
  color: #5a7184;
  font-family: "Roboto";
  font-size: 18px;
}

.card-heading {
  font-size: 25px;
  font-family: "Roboto";
}
.linkemail {
  text-decoration: none;
}
.card-image {
  height: 70px;
  width: 60px;
  margin-top:15px;
  border-radius: 25%;
}

.card-container {
  background-color: white;
  text-align: center;
  border-radius: 8px;
  border-width: 1px;
  border-style: solid;
  border-color: #e5eaf4;
}


.horizontalline {
  width: 90%;
  color: width 3px solid rgb(100, 126, 130);
  margin-bottom: 5%;
}

.headintop {
  margin-top: 100px;
  text-align: center;
  padding-top: 15px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 45px;
  color:white;
}

.app-container {
  box-sizing: border-box;
  max-width: 1110px;
  margin: auto;
  justify-content:center;
  display:flex;
  align-items:center;
  flex-direction:column;
  background-image: url('https://res.cloudinary.com/dhzqndogf/image/upload/v1707469415/dark-geometric-background-with-copy-space_nijseg.jpg');
  background-size: cover;
}
.QandAcontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: auto;
}
.title {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 24px;
  margin-top: 80px;
  line-height: 56px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 0;
}

@media screen and (min-width: 992px) {
  .title {
    font-size: 32px;
    margin-top: 100px;
    margin-bottom: 24px;
    color: white;
  }

  .horizontalline {
    width: 110%;
    color: width 3px solid rgb(100, 126, 130);
    margin-bottom: 5%;
  }
}

.description {
  max-width: 300px;
  font-family: 'Roboto';
  font-size: 16px;
  margin-top: 10px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  margin: auto;
  margin-bottom: 40px;
}

@media screen and (min-width: 992px) {
  .description {
    max-width: 730px;
    font-size: 18px;
    margin-top: 20px;
    line-height: 28px;
  }
}

.tabs-container {
  max-width: 400px;
  margin: auto;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
}

@media screen and (min-width: 992px) {
  .tabs-container {
    max-width: 600px;
    margin-bottom: 25px;
  }
}

.project-list-container {
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 12px;
  margin-bottom: 50px;
}

@media screen and (min-width: 992px) {
  .project-list-container {
    margin-bottom: 150px;
  }
}

@media screen and (min-width: 250px) {
  .title {
    font-size: 32px;
    margin-top: 140px; /* Adjust the top margin for larger screens */
  }

  .description {
    font-size: 18px;
    margin-top: 20px; /* Adjust the top margin for larger screens */
  }
}

.educationdetails {
  max-width:1100px;
  width:100%;
  display:grid;
  grid-template-columns:repeat(auto-fit, minmax(300px,1fr));
  gap:1rem
}

.move-to-top-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
}

.move-to-top-button:hover {
  background-color: #0056b3;
}

.usernametext {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 18px;
  color: #ffffff;
  text-align: center;
}
.project-link:hover {
  animation: shake 1s ease infinite; /* Add animation */
  box-shadow: 0 0 100px rgba(0, 255, 0, 1); /* Change box shadow to light green with full brightness */
}


.download-container {
  position: fixed;
  top: 12%; /* Adjust top spacing for large screens */
  right: 0px; /* Adjust right spacing for large screens */
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  width: auto; /* Remove width: 100% */
  z-index: 999; /* Ensure the button is above other content */
}

.download-btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #4CAF50; /* Green background color */
  color: white;
  font-size: 16px;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease; /* Add transition for smooth color change */
}

.download-btn:hover {
  background-color: #2e63cd; /* Darker green color on hover */
}

/* Media query for small screens */
@media (max-width: 768px) {
  .download-container {
    position: fixed;
    bottom: 20px; /* Adjust bottom spacing for small screens */
    left: 50%; /* Center the button horizontally for small screens */
    transform: translateX(-50%);
    top: auto; /* Remove top spacing for small screens */
    right: auto; /* Remove right spacing for small screens */
  }
}



/* Media queries for responsive design */
@media (max-width: 768px) {
  .download-container {
    bottom: 10px; /* Adjust bottom spacing for smaller screens */
    left: 50%; /* Center the button horizontally on smaller screens */
    transform: translateX(-50%);
  }
}



@media screen and (max-width: 768px) {
  .educationdetails {
    grid-template-columns: 1fr;
    padding-left:5%;
  }.STUDIESCARD {
    margin-left:4%;
  }
  .card-container {
  margin-right:6%;
  }
}

.skilsone {
  display:flex;
  justify-content:center;
  align-items:center;
}
.STUDIESCARD {
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  line-height: 2px;
  background: linear-gradient(to right top, transparent 50%, rgba(75, 93, 101, 1) 50%);
  background-size: 200% 200%;
  background-position: left bottom;
  transition: background-position 0.9s ease; 
}

.STUDIESCARD:hover {
  background-position: right top; 
}




.Educationheading {
  text-align:center;
  color:wheat;
}


