.card-container-home {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    border-radius: 8px;
    width:svw;
    background-image: url('https://res.cloudinary.com/dhzqndogf/image/upload/v1707469415/dark-geometric-background-with-copy-space_nijseg.jpg');
    background-size: cover;
    overflow: hidden;
    padding:2%;
  }
  

  .download-container {
    text-align: right; /* Align content to the right */
    margin-top: 20px; /* Adjust margin as needed */
  }
  
  .download-btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4CAF50; /* Green background color */
    color: white;
    font-size: 16px;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease; /* Add transition for smooth color change */
  }
  
  .download-btn:hover {
    background-color: #45a049; /* Darker green color on hover */
  }
  

/* Write some CSS code for the cursor */
.cursor {
  /* Use a border to create the cursor shape */
  border-left: 4px solid black;
  /* Use a margin to adjust the cursor position */
  margin-left: 5px;
  /* Use an animation to make the cursor blink */
  animation: blink 0.5s infinite;
}

/* Define the animation keyframes */
@keyframes blink {
  /* Hide the cursor at 0% and 100% */
  0%, 100% {
    opacity: 0;
  }
  /* Show the cursor at 50% */
  50% {
    opacity: 1;
  }
}


  .card-home {
    display:flex;
    align-items:center;
    justify-content:center;
    width: 300px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
  }
  
  .card-home:hover {
    box-shadow: 0 8px 16px rgba(0, 123, 255, 0.5);
  }
  
  .myimage {
    border-radius: 50%;
    width: 150px;
    transition: transform 0.3s ease; /* Add scaling transition */
  }
  
  .myimage:hover {
    transform: scale(1.1); /* Increase scale on hover for a closer effect */
  }
  
  .myname {
    font-family: 'Times New Roman', Times, serif;
    color:white;
    text-align:center;
    line-height:1;
  }
  
  .skillsContainer {
    max-width:1000px;
    width:100%;
    display:grid;
    grid-template-columns:repeat(auto-fit, minmax(100px,1fr));
    gap:2rem
  }
  
  @keyframes loadingDots {
    0%,
    20% {
      content: 'Loading';
    }
    40% {
      content: 'Loading.';
    }
    60% {
      content: 'Loading..';
    }
    80%,
    100% {
      content: 'Loading....';
    }
  }
  
  .loading-text::before {
    color:white;
    content: 'Loading';
    animation: loadingDots 1.5s infinite steps(1);
    display: inline-block;
  }
  
  
  .skilllog {
    transition: transform 0.1s ease; /* Add scaling transition */
    
  }
  
  .skilllog:hover {
    transform: scale(1.5); /* Increase scale on hover for a closer effect */
  }
  
  
  @media screen and (min-width: 800px) {
    .myname {
      background: -webkit-linear-gradient(45deg, violet, indigo, blue, green, yellow, orange, red);
      -webkit-background-clip: text;
      background-clip:text;
      color: transparent;
      text-align:center;
    }
  }
  
  @media screen and (max-width: 700px) {
    .card-container-home  {
      padding-left:3%;
      padding-right:2%;
      width:dvw;
      height:auto;
    }

    .cursor {
      /* Use a border to create the cursor shape */
      border-left: 4px solid rgb(62, 129, 192);
  }

 
  .skillsContainer {
    height:auto;
    max-width:400px;
    display:grid;
    grid-template-columns:no repeat(auto-fit, minmax(50px,1fr));
    gap:1rem
  }
  }